  
<template>
  <div class="contents">
    <up-load :value="json" @input="json = $event"></up-load>
    <file-export :json="json"></file-export>
    <div  v-for="category in json" :key="category.id">
      <button @click="clickCategory(category.id)" class="course-name btn btn-outline-secondary btn-sm">{{category.id}}</button>
      <div v-if="openCategoryList.indexOf(category.id) !==-1">
        <div v-for="course in category.courses" :key="course.id">
            <button @click="click(course.id)" class="categoryBtn btn btn-outline-secondary btn-sm">{{course.title}}</button>
          <ul>
            <div v-if="openCourseList.indexOf(course.id) !==-1">
              <ul class="none">
                <li>{{"title:" + course.title}}</li>
                    <input v-model="course.title" id="course.id" class="form-control form-control-sm">

                <li>{{"type:" + course.type}}</li>
                    <input v-model="course.type" id="course.id" class="form-control form-control-sm">
                
                <li>{{"id:" + course.id}}</li>
                    <input v-model="course.id" class="form-control form-control-sm">

                <li>{{"description:" + course.description}}</li>
                  <input v-model="course.description" class="form-control form-control-sm">

                <li>{{"author:" + course.author}}</li>
                  <input v-model="course.author" class="form-control form-control-sm">

                <div v-if="course.type==='WebPages'">
                <li>{{"articles"}}</li>
                  <input v-model="course.article" class="article form-control form-control-sm">
                  <button @click="article(category.id,course.id)" class="articleBtn btn btn-secondary btn-sm">追加</button>
                  <ol>
                    <li v-for="(article,index) in course.articleURLs" :key="article">{{article}}               
                      <input v-model="course.articleURLs[index]" class="article-url form-control form-control-sm">
                    </li>
                  </ol>
                </div>
                <button @click="delCourse(category.id,course.id,course.title)" class="del-course btn btn-danger btn-sm">コース削除</button>
              </ul>
            </div>
          </ul>
        </div>
      <button @click="newCourse(category.id)" class="new-course btn btn-secondary btn-sm">コース新規作成</button>
      <button @click="delCategory(category.id)" class="del-category btn btn-danger btn-sm">カテゴリー削除</button>
      </div>
    </div>
    <div class="new-category">
      <input v-model="message" class="msg">
      <button @click="newCategory()" class="new-category-btn btn btn-outline-secondary btn-sm">カテゴリー新規作成</button>
    </div>
    
  </div>
</template>

<script>
import FileExport from './FileExport'
import UpLoad from './UpLoad'
export default {
  name: 'Data',
  components: {
    FileExport,
    UpLoad
  },
  data(){
    return{
      category:null,
      openCategoryList:[],
      openCourseList:[],
      json:null
    }
  },
  methods: {
    clickCategory(categoryid){
      if (this.openCategoryList.indexOf(categoryid) !== -1){
        this.openCategoryList= this.openCategoryList.filter(item => item !== categoryid)
      }else{
        this.openCategoryList.push(categoryid)
      }
    },
    click(courseid) {
      if (this.openCourseList.indexOf(courseid) !== -1){
        this.openCourseList=this.openCourseList.filter(item => item !== courseid)
      }else{
        this.openCourseList.push(courseid)
      }
    },
    article(category,course){
      if(this.getCourseById(category,course).articleURLs){
        this.getCourseById(category,course).articleURLs.push(document.getElementsByClassName("article")[0].value)
      }else{
        this.getCourseById(category,course).articleURLs = [document.getElementsByClassName("article")[0].value
        ]}
    },
    newCourse(category){
      this.getCategoryById(category).courses.push({author: "",description: "",id: "",title: "新しいコース",type: ""})
    },
    newCategory(){
      this.category = document.getElementsByClassName("msg")[0].value
      if(this.category){
        if(this.json==null){
          this.json=[{id:this.category,courses:[{author: "",description: "",id: "",title: "新しいコース",type: ""}]}]
        }
        else{
          this.json.push({id:this.category,courses:[]})
          console.log(this.json)
          }  
      }
    },
    delCourse(category,course,title){
      if(confirm(title+'を削除しますか？')){
        this.getCategoryById(category).courses.splice(this.getCourseIndex(category,course),1)
      }
    },  
    delCategory(category){
      if(confirm(this.getCategoryById(category).id+'を削除しますか？')){
        console.log(this.json)
        this.json = this.json.filter(item => {return(item.id!==this.getCategoryById(category).id)})
        console.log(this.json)
      }
    },
    getCategoryById(categoryid){
      return this.json.find((item) =>  (item.id === categoryid))
    },
    getCourseById(categoryid,courseid){
      return this.getCategoryById(categoryid).courses.find((item) => (item.id === courseid))
    },
    getCourseIndex(categoryid,courseid){
      return this.getCategoryById(categoryid).courses.findIndex((item) => (item.id === courseid))
    },
  },
}
</script>

