  width:600px;
  margin:5px auto;
}

.none{
  list-style: none;
}

.categoryBtn{
  margin:5px 10px;
}

.btn{
  border-radius: 5px;
}

.del-btn{
  margin:10px 0;
  display: block;
}

.msg{
  margin:0 5px 0 15px;
}

.new-category{
  margin: 5px 0 0 5px;
  display: block;
}

.article{
  display: inline;
}
.articleBtn{
  margin-left:5px;
  display: inline;
}

.new-course{
  margin:5px 0 5px 350px;
}

.del-course{
  display: block;
  margin:10px 0 0 0;
}

