<template>
    <div class="data-upload">
      <label for="data_upload" class="btn btn-secondary btn-sm">ファイル(JSON)を選択
        <input type="file" id="data_upload" name="data_upload" class="" @change="onFileChange">
      </label>
    </div>
</template>

<script> 
export default{
  props:{
    value:Object
  },
  computed:{
      json: {
      get: function() {
        return this.value
      },
      set: function(value) {
        this.$emit('input', value) 
      },
    },
  },
  methods:{
      onFileChange:function(e){
        var file = e.target.files[0]
        if(file){
          var reader = new FileReader()
          reader.onload = e=>{
          this.json = JSON.parse(e.target.result);
          }
          reader.readAsText(file)
        }
        return this.json
      },
  
  },
}
</script>
<style scoped>
label > input {
  display:none; 
}

label {
  margin:0 7px 0 0;
}

.data-upload{
  display: inline-block;
}

</style>