<template>
  <div class="div">
    <button @click="FileExport()" class="btn btn-secondary btn-sm">エクスポート</button>
  </div>
</template>

<script>
export default{
  name:'FilExport',
  props:['json'],
  components:{
  },
  data(){
    return{
      filename:"toc_new.json",
      filedata:null,
      link:null
    }
  },
  methods:{
    FileExport(){
      this.filedata = JSON.stringify(this.json)
      this.link = document.createElement('a')
      this.link.href = "data:text/plain," + encodeURIComponent(this.filedata) 
      this.link.download = this.filename
      this.link.click()
    }

  },
}
</script>
<style scoped>
.export{
  font-size: 15px;
  border: 2px solid #000;
  background: rgb(212, 212, 212);
  margin: 5px 0 0 10px;

}
.export:hover {
  color: #fff;
  background: #000;
}

.div{
  display: inline-block;
}
</style>